"use client"

import { useEffect, useRef, useState } from "react"

export default function Hero() {
  const ref = useRef<HTMLDivElement>(null)
  const [isButtonHovered, setIsButtonHovered] = useState(false)

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting) {
          entry.target.classList.add("animate-fade-in-up")
        }
      },
      { threshold: 0.1 },
    )

    if (ref.current) observer.observe(ref.current)
    return () => observer.disconnect()
  }, [])

  return (
    <section className="relative min-h-screen flex items-center justify-center overflow-hidden">
      {/* Background glow */}
      <div className="absolute inset-0 -z-10">
        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-[400px] h-[400px] bg-primary/3 rounded-full blur-3xl" />
      </div>

      <div ref={ref} className="relative z-10 w-full max-w-3xl mx-auto px-6 text-center">
        <div className="flex flex-col items-center justify-center gap-6">
          {/* Logo */}
          <div className="text-xl sm:text-2xl font-light tracking-[0.5em] uppercase progressive-glow-text">
            SKYGNOSIS
          </div>

          {/* Main heading */}
          <h1 className="text-5xl sm:text-6xl md:text-7xl font-extralight tracking-tight text-sharp-white animate-glow-heading">
            Elevates Intelligence
          </h1>

          {/* Description */}
          <p className="text-sm sm:text-base text-mist-gray/70 font-extralight tracking-wide max-w-md mt-4">
            Your intelligent AI Solution for business transformation
          </p>

          {/* CTA Button - Optimized */}
          <div className="mt-12">
            <button 
              className="group relative px-10 py-4 bg-gradient-to-r from-blue-600 to-blue-500 text-white font-medium text-base tracking-wide rounded-xl overflow-hidden transition-all duration-300 hover:scale-105 active:scale-100"
              onMouseEnter={() => setIsButtonHovered(true)}
              onMouseLeave={() => setIsButtonHovered(false)}
            >
              {/* Base glow */}
              <div 
                className="absolute inset-0 rounded-xl transition-opacity duration-500"
                style={{ 
                  boxShadow: '0 0 30px rgba(96, 165, 250, 0.6)',
                  opacity: isButtonHovered ? 1 : 0.6 
                }} 
              />

              {/* Animated gradient (only 1 layer) */}
              <div 
                className="absolute inset-0 bg-gradient-to-r from-blue-400 via-purple-500 to-blue-600 opacity-0 group-hover:opacity-100 transition-opacity duration-500"
                style={{ backgroundSize: '200% 100%' }} 
              />

              {/* Shimmer on hover only */}
              {isButtonHovered && (
                <div className="absolute inset-0 animate-shimmer-once bg-gradient-to-r from-transparent via-white/30 to-transparent" />
              )}

              {/* Reduced particles - only 4 */}
              {isButtonHovered && (
                <div className="absolute inset-0 pointer-events-none">
                  {[...Array(4)].map((_, i) => (
                    <div
                      key={i}
                      className="absolute w-1 h-1 bg-white rounded-full animate-particle-simple"
                      style={{
                        top: '50%',
                        left: '50%',
                        animationDelay: `${i * 0.3}s`,
                        transform: `rotate(${i * 90}deg) translateY(0px)`,
                      }}
                    />
                  ))}
                </div>
              )}

              {/* Simplified corners - only show on hover */}
              {isButtonHovered && (
                <>
                  <div className="absolute top-1 left-1 w-6 h-6 border-t-2 border-l-2 border-white/70 transition-opacity duration-300" />
                  <div className="absolute bottom-1 right-1 w-6 h-6 border-b-2 border-r-2 border-white/70 transition-opacity duration-300" />
                </>
              )}

              {/* Button text */}
              <span className="relative z-10 flex items-center justify-center gap-3">
                <span className="w-2 h-2 bg-green-400 rounded-full animate-pulse-slow shadow-lg shadow-green-400/50" />
                Start Consultation
                <svg className="w-4 h-4 transition-transform group-hover:translate-x-1 duration-300" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 7l5 5m0 0l-5 5m5-5H6" />
                </svg>
              </span>
            </button>
          </div>

          {/* Stats */}
          <div className="flex items-center gap-8 mt-20 text-xs text-mist-gray/40">
            <span>500+ Projects</span>
            <span className="w-1 h-1 rounded-full bg-mist-gray/20" />
            <span>98% Satisfaction</span>
            <span className="w-1 h-1 rounded-full bg-mist-gray/20" />
            <span>15+ Years</span>
          </div>
        </div>
      </div>
    </section>
  )
}
