"use client"

import { motion } from "framer-motion"
import { useState, useRef } from "react"

export default function TrustedBy() {
  const companies = ["TechCorp", "DataFlow", "CloudSync", "NeuralNet", "VelocityAI", "QuantumLeap"]
  const allCompanies = [...companies, ...companies]

  return (
    <section className="relative py-16 md:py-24 px-4 overflow-hidden">
      <div className="max-w-7xl mx-auto">
        <p className="text-center text-foreground/50 text-sm font-medium uppercase tracking-wider mb-12">
          Trusted by Leading Organizations
        </p>

        <div className="relative">
          <div className="absolute left-0 top-0 bottom-0 w-32 bg-gradient-to-r from-background to-transparent z-10" />
          <div className="absolute right-0 top-0 bottom-0 w-32 bg-gradient-to-l from-background to-transparent z-10" />

          <motion.div
            className="flex gap-8 py-4"
            animate={{ x: [0, -1920] }}
            transition={{
              x: { duration: 40, repeat: Infinity, ease: "linear" },
            }}
          >
            {allCompanies.map((company, i) => (
              <SimpleCard key={i} company={company} />
            ))}
          </motion.div>
        </div>
      </div>
    </section>
  )
}

function SimpleCard({ company }: { company: string }) {
  const [isHovered, setIsHovered] = useState(false)

  return (
    <motion.div
      className="flex-shrink-0 px-10 py-6 min-w-[220px] flex items-center justify-center relative group"
      onHoverStart={() => setIsHovered(true)}
      onHoverEnd={() => setIsHovered(false)}
      whileHover={{ scale: 1.05 }}
      transition={{ duration: 0.2 }}
    >
      <div className="absolute inset-0 glass rounded-xl border border-blue-500/10 group-hover:border-blue-500/40 transition-colors duration-300" />
      
      <p className="relative z-10 text-foreground/60 group-hover:text-foreground/90 font-medium transition-colors duration-300">
        {company}
      </p>
    </motion.div>
  )
}
